<template>
<el-dialog class="add-exchange-popup" title="新增" v-model="showDialog" width="800">
    <el-form :model="form" label-position="left" label-width="100px">
        <el-form-item label="交換">
            <el-radio-group v-model="form.type">
                <el-radio label="同類型交換"  value="same"></el-radio>
                <el-radio label="不同類型交換"  value="def"></el-radio>
            </el-radio-group>
        </el-form-item>
        <el-form-item label="組合">
            <el-select v-model="form.group" placeholder="请選擇組合">
                <el-option v-for="(member,i) in groups" :label="member"  :value="member" :key="i"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="物品類型">
            <el-radio-group v-model="form.category">
                <el-radio v-for="(category,i) in categories" :label="category"  :value="category" :key="i"></el-radio>
            </el-radio-group>
        </el-form-item>
        <el-form-item v-if="form.category === '專卡'">
            <el-select v-model="form.album" placeholder="请選擇專輯">
                <el-option v-for="(album,i) in albums" :label="album"  :value="album" :key="i"></el-option>
            </el-select>
        </el-form-item>
        <el-upload
            action="https://jsonplaceholder.typicode.com/posts/"
            list-type="picture-card"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove">
        <i class="el-icon-plus"></i>
        </el-upload>
        <el-form-item v-if="form.category">
            <el-input v-model="form.name" placeholder="請詳述物品"></el-input>
        </el-form-item>
        <el-form-item label="擁有成員">
            <el-select v-model="form.region" placeholder="请選擇成員">
                <el-option v-for="(member,i) in members" :label="member"  :value="i" :key="i"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="欲交換成員">
            <el-checkbox-group v-model="form.target">
                <el-checkbox v-for="(member,i) in members" :label="member"  :key="i"  name="type"></el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <el-form-item label="交易方式">
            <el-checkbox-group v-model="form.ways">
                <el-checkbox v-for="(way,i) in ways" :label="way"  :key="i"  name="way"></el-checkbox>
            </el-checkbox-group>
        </el-form-item>
    </el-form>
    <template #footer>
        <span class="dialog-footer">
        <el-button>取 消</el-button>
        <el-button type="primary">确 定</el-button>
        </span>
    </template>
    </el-dialog>
</template>

<script>
import { mapState } from 'vuex'

export default {
    props: {
        showDialog: {
            type: Boolean,
            require: true
        }
    },
    data(){
        return {
            form: {
                type:'',
                group: '',
                album: '',
                category: '',
                date1: '',
                date2: '',
                delivery: false,
                ways: [],
                target: [],
                resource: '',
                desc: ''
            },
            members:[],
            albums:[],
            categories:['專卡','玩偶/娃娃','官方周邊','飯制周邊'],
            ways: ['店到店','賣貨便','面交','郵寄','其他']
        }
    },
    mounted(){
    },
    computed:{
        ...mapState({
            groups: state => state.basic.groups,
            nctMembers: state => state.basic.nctMembers,
            nct127Members: state => state.basic.nct127Members,
            nctdreamMembers: state => state.basic.nctdreamMembers,
            wayvMembers: state => state.basic.wayvMembers,
            nctAlbums: state => state.basic.nctAlbums,
            nct127Albums: state => state.basic.nct127Albums,
            nctdreamAlbums: state => state.basic.nctdreamAlbums,
            wayvAlbums: state => state.basic.wayvAlbums,
        }),
    },
    watch:{
        'form.group':{
            handler(newValue){
                console.log(newValue)
                switch(newValue){
                case 'NCT':
                    this.members = this.nctMembers
                    this.albums = this.nctAlbums
                    break;
                case 'NCT127':
                    this.members = this.nct127Members
                    this.albums = this.nct127Albums
                    break;
                case 'NCT Dream':
                    this.members = this.nctdreamMembers
                    this.albums = this.nctdreamAlbums
                    break;
                case 'WayV':
                    this.members = this.wayvMembers
                    this.albums = this.wayvAlbums
                    break;
            }
            }
        }
    },
    methods:{
        onClose(){
            this.$emit('update:dialogVisible', false)
        },
    }


}
</script>

<style lang="scss" scoped>
.add-exchange-popup {
}

</style> 